import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	 useEffect(() => {
		//connect.send("VKWebAppGetCommunityAuthToken", {"app_id": 7184325, "group_id": 137849330, "scope": "app_widget"});///Виджет
		/* https://api.vk.com/method/appWidgets.update?type=list&code=return
		{
      "title": "Каникулы",
      "rows": [{
          "title": "Осенние каникулы",
          "descr": "🌧 Осенние каникулы начнутся 28-го октября и продлятся до 4-го ноября."
        }, {
          "title": "Зимние каникулы",
          "descr": "❄ Зимние каникулы начнутся 30-го декабря и продлятся до 12-го января."
    }, {
          "title": "Весенние каникулы",
          "descr": "🌱 Весенние каникулы начнутся 23-го марта и продлятся до 30-го марта."
    }, {
          "title": "Отсчёт",
          "descr": "⏱ Каникулы наступят через 2 дня"
    }]
};
&access_token=6a595edc87dc512289f0076783354fab1708490e39bc6a282744ea9be5800143e7c62b704f943ab0e0921&v=5.104 */
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await connect.sendPromise('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} />
		</View>
	);
}

export default App;

